/* input */
input[type="text"],
input[type="email"],
input[type="tel"],
input[type="number"],
input[type="password"],
select,
textarea {
  position: relative;
  background-color: #FBFBFB;
  border: 1px solid #888888;
  box-sizing: border-box;
  padding: 0.8rem 0.8rem;
  width: 100%;
  font-family: "Yu Gothic", sans-serif;
  font-weight: 500;
  font-size: 1.3rem;
  line-height: 2.4rem;
  text-align: left;
  letter-spacing: 0.08em;
  color: #3E3A39;
}

input[type="text"],
input[type="email"],
input[type="tel"],
input[type="number"],
input[type="password"],
select {
  height: 4.0rem;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="tel"]:focus,
input[type="number"]:focus,
input[type="password"]:focus,
select:focus,
textarea:focus {
  border-color: #888888;
}

select {
  position: relative;
  -webkit-appearance: none;
  -moz-appearance: none;
  background-image: url(../img/select-down.png);
  background-position: calc(100% - 0.8rem) 50%;
  background-size: 1.6rem 1.6rem;
  background-repeat: no-repeat;
  z-index: 0;
}

/* checkbox */
.checkbox {
  display: block;
  position: relative;
  padding-left: 3.2rem;
  cursor: pointer;
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 3.0rem;
  text-align: left;
  color: #000000;
  user-select: none;
}

@media only screen and (max-width: 768px) {
  .checkbox {
    font-size: 1.6rem;
    line-height: 2.8rem;
    padding-left: 3.0rem;
  }
}

.checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkbox .checkmark {
  position: absolute;
  top: 0.3rem;
  left: 0;
  height: 2.4rem;
  width: 2.4rem;
  background-color: #FFFFFF;
  border-radius: 0.4rem;
  border: 2px solid #1B1B1F;
}

@media only screen and (max-width: 768px) {
  .checkbox .checkmark {
    top: 0.4rem;
    width: 2.0rem;
    height: 2.0rem;
    border-radius: 0.4rem;
  }
}

.checkbox input:focus ~ .checkmark {
  outline: 0.3rem solid #7EBF41;
}

.checkbox input:checked ~ .checkmark {
  background-color: #3B8E65;
  border-color: #3B8E65;
  outline: none;
}

.checkbox .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.checkbox input:checked ~ .checkmark:after {
  display: block;
}

.checkbox .checkmark:after {
  left: 0.5rem;
  top: 0;
  width: 0.9rem;
  height: 1.5rem;
  border: solid #FFFFFF;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

@media only screen and (max-width: 768px) {
  .checkbox .checkmark:after {
    left: 0.4rem;
    top: 0;
    width: 0.8rem;
    height: 1.2rem;
  }
}

picture,
figure,
.image_wrap {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

picture img,
figure img,
.image_wrap img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
}

.scroll {
  position: relative;
  max-width: 100%;
  width: 100%;
  overflow: auto;
}

@media only screen and (max-width: 768px) {
  .scroll {
    padding-bottom: 1.2rem;
  }
  .scroll::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
    background-color: #f5f5f5;
    border-radius: 0.6rem;
  }
  .scroll::-webkit-scrollbar {
    height: 0.6rem;
    background-color: #f5f5f5;
  }
  .scroll::-webkit-scrollbar-thumb {
    border-radius: 0.6rem;
    background-color: #FED517;
  }
}

.animation-btn:link,
.animation-btn:visited {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20.8rem;
  height: 5.0rem;
  color: #333;
  border: 3px solid #333;
  border-radius: 90px;
  /* 112.5% */
  overflow: hidden;
  text-shadow: 0 0 1px rgba(0, 0, 0, 0.2), 0 1px 0 rgba(0, 0, 0, 0.2);
  transition: all 1s ease;
}

.animation-btn:link:after,
.animation-btn:visited:after {
  content: "";
  position: absolute;
  height: 0%;
  left: 50%;
  top: 50%;
  width: 150%;
  z-index: 0;
  transition: all 0.4s ease 0s;
}

.animation-btn:link span,
.animation-btn:visited span {
  position: relative;
  z-index: 3;
  transition: all 0.4s ease 0s;
}

.animation-btn:link:hover span,
.animation-btn:visited:hover span {
  text-shadow: none;
}

.animation-btn:link:hover:after,
.animation-btn:visited:hover:after {
  height: 450%;
}

.btn-yellow {
  background: #FFFE3B;
  border: 2px solid #333;
}

.btn-yellow span {
  color: #333;
}

.btn-yellow:hover span {
  color: #FFF;
}

.btn-yellow:after {
  background: #333;
  opacity: .5;
  -moz-transform: translateX(-50%) translateY(-50%);
  -ms-transform: translateX(-50%) translateY(-50%);
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
  z-index: 10;
}

.btn-yellow:hover:after {
  height: 140%;
  opacity: 1;
}

.btn-black {
  background: #333;
  border: 2px solid #333;
}

.btn-black span {
  color: #FFF;
}

.btn-black:hover span {
  color: #333;
}

.btn-black:after {
  background: #FFFE3B;
  opacity: .5;
  -moz-transform: translateX(-50%) translateY(-50%);
  -ms-transform: translateX(-50%) translateY(-50%);
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
  z-index: 10;
}

.btn-black:hover:after {
  height: 140%;
  opacity: 1;
}

